import './index.scss';
import { MySwiper } from "~/components/swipe";
const SwiperItem = MySwiper.Item;
export default class Guide extends Component {
    constructor() {
        super()
        this.state = {
            imgs: [
                require("@/assets/images/201805101137396523.jpg"),
                require("@/assets/images/1073186.jpg"),
                require("@/assets/images/201805101137396143.jpg"),
                require("@/assets/images/201805141537031185.jpg"),
            ]
        }
    }
    componentDidMount() {
        //-------------------------
        for (var i = 0; i < 10; i++) {
            var d = document.createElement("div");
            d.className = "qipao";
            var cm = rand(10, 150);
            d.style.width = cm + 'px';
            d.style.height = cm + 'px';

            d.style.left = rand(0, window.innerWidth - 150) + 'px';
            d.style.top = rand(0, window.innerHeight - 150) + 'px';

            var r = rand(0, 255);
            var g = rand(0, 255);
            var b = rand(0, 255);
            var color = `rgb(${r},${g},${b})`;
            d.style.backgroundColor = color;

            var op = Math.random();
            d.style.opacity = op;

            //			手动为div创造一个属性speed
            d.speedOpacity = 0.01;
            if (0.5 - Math.random() > 0) {
                d.speedOpacity = -0.01;
            }
            d.speedX = rand(2, 4);
            if (0.5 - Math.random() > 0) {
                d.speedX *= -1;
            }
            d.speedY = rand(2, 4);
            if (0.5 - Math.random() > 0) {
                d.speedY *= -1;
            }
            var qipao = document.querySelector(".qipao");
            qipao.appendChild(d);
        }

        function rand(min, max) {
            return Math.round(Math.random() * (max - min) + min);
        }

        function changeOpacityAndPosition() {
            var ds = document.getElementsByTagName("div");
            for (var i = 0; i < ds.length; i++) {
                var d = ds[i];

                var op = d.style.opacity;
                op = parseFloat(op);
                op += d.speedOpacity;
                if (op >= 1 || op <= 0) {
                    d.speedOpacity *= -1;
                }
                d.style.opacity = op;

                var x = d.offsetLeft + d.speedX;
                var y = d.offsetTop + d.speedY;
                if (x < 0) {
                    x = 0;
                    d.speedX *= -1;
                } else if (x > window.innerWidth - d.offsetWidth) {
                    x = window.innerWidth - d.offsetWidth;
                    d.speedX *= -1;
                }

                if (y < 0) {
                    y = 0;
                    d.speedY *= -1;
                } else if (y > window.innerHeight - d.offsetHeight) {
                    y = window.innerHeight - d.offsetHeight;
                    d.speedY *= -1;
                }

                d.style.left = x + 'px';
                d.style.top = y + 'px';
            }
        }
        setInterval(function () {
            changeOpacityAndPosition()
        }, 30);
        //-----------------------------
        if (localStorage.visitCount) {
            localStorage.visitCount++;
            if (localStorage.visitCount > 300) {
                this.props.history.push('/index/home')
            }
        } else {
            localStorage.visitCount = 1;
        }
    }
    gotoApp = (id) => {
        if (id = this.state.imgs.length - 1) {
            const { history } = this.props;
            history.push("/index/home");

        }
    }
    render() {
        const {
            imgs
        } = this.state
        return (
            <div className="guide">
                <div className="qipao" style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: 100 }}>
                </div>
                <MySwiper id="my-guide"
                    swiperOptions={{
                        speed: 1000,
                        autoplay: {
                            delay: 1000,
                            stopOnLastSlide: true,
                            disableOnInteraction: false,
                        },
                    }}
                >
                    {
                        imgs.map((m, i) => {
                            return (
                                <SwiperItem key={i}>
                                    <img src={m}
                                        onClick={() => this.gotoApp(i)}
                                        className="g-img"
                                        alt=""
                                    />
                                    {i == imgs.length - 1 && <button onClick={() => this.props.history.push('/index/home')} className="entry-btn">点击进入</button>}
                                </SwiperItem>
                            )
                        })
                    }
                </MySwiper>
            </div>
        )
    }
}